import React from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import Box from '@material-ui/core/Box';

import productSubscriptionType from 'components/__types__/productSubscription';
import ProductSubscriptionFieldTable from 'components/product-subscription-field-table/ProductSubscriptionFieldTable';

const ProductSubscriptionDetails = ({ t, productSubscription }) => {
  return (
    <Box>
      <h3>
        {t('common.widgetName', {
          widgetNamePlaceholder: 'Product Subscription',
        })}
      </h3>
      <ProductSubscriptionFieldTable productSubscription={productSubscription} />
    </Box>
  );
};

ProductSubscriptionDetails.propTypes = {
  productSubscription: productSubscriptionType,
  t: PropTypes.func.isRequired,
};

ProductSubscriptionDetails.defaultProps = {
  productSubscription: {},
};

export default withTranslation()(ProductSubscriptionDetails);
